import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import 'bootstrap/dist/css/bootstrap.css';

const music = [
  {id: "Accordion", innerText: "Q", src: "http://dight310.byu.edu/media/audio/FreeLoops.com/1/1/Accordian%20A2%20Sample-4173-Free-Loops.com.mp3"},
  {id: "Drums", innerText: "W", src: "http://dight310.byu.edu/media/audio/FreeLoops.com/4/4/Hard%20House.wav-7959-Free-Loops.com.mp3"},
  {id: "Guitar", innerText: "E", src: "http://soundcavern.free.fr/guitar/Acoustic_Guitar_Chords%20-%20Amaj.wav"},
  {id: "Harp", innerText: "A", src: "http://www.sounds.beachware.com/2illionzayp3may/puzaytua/HARPINIC.mp3"},
  {id: "Piano", innerText: "S", src: "http://dight310.byu.edu/media/audio/FreeLoops.com/3/3/Dx%20Piano%20Chord%20D5-4146-Free-Loops.com.mp3"},
  {id: "Saxophone", innerText: "D", src: "http://newt.phys.unsw.edu.au/music/saxophone/soprano/sounds/A4.mp3"},
  {id: "Tamborine", innerText: "Z", src: "http://dight310.byu.edu/media/audio/FreeLoops.com/5/5/Native%20American-6093-Free-Loops.com.mp3"},
  {id: "Violin", innerText: "X", src: "http://www.tremblingsandwarblings.com/wp-content/uploads/2017/05/over-board-new.mp3"},
  {id: "Xylophone", innerText: "C", src: "http://onj3.andrelouis.com/phonetones/unzipped/Meizu%20MX4/notifications/Naughty.mp3"}
]

const activeStyle = {
    backgroundColor: 'silver',
    boxShadow: "0 3px silver",
    color: "black",

   }
  
  const inactiveStyle = {
    backgroundColor: 'black',
    boxShadow: "3px 3px 5px black"
  }

  //<DrumPad component for attaching sound, updating display, changing color background of every keys
class DrumPad extends React.Component{
    constructor(props) {
        super(props);
        this.state = {
            padStyle: inactiveStyle
          }
        this.playAndDisplay = this.playAndDisplay.bind(this);
        this.handleKeydown = this.handleKeydown.bind(this);
        this.activePad = this.activePad.bind(this);
    }

playAndDisplay = () => {
    this.audio.play();                  // this.audio is predetermined function to play audio
    this.audio.currentTime = 0;         // necessary for smooth transition of sounds
    this.props.updateDisplay(this.props.describeId); // update display
    this.activePad();                   // change background color of keys
    setTimeout(() => this.activePad(), 200);
}

// Lifecycles and hanleKeydown function to attach laptop/computer keys to keys or pad of app
componentDidMount() {
    document.addEventListener("keydown", this.handleKeydown)
}

componentWillUnmount() {
    document.removeEventListener("keydown",this.handleKeydown)
}

handleKeydown = (e) => {
    if(e.keyCode === this.props.keyName.charCodeAt(0)) {
        this.playAndDisplay(); //calling function
    }
}

// function to change color of pads each time it is being pressed
activePad() {
if (!this.props.power) {
    this.state.padStyle.backgroundColor === 'silver' ?
      this.setState({
        padStyle: inactiveStyle
      }) :
      this.setState({
        padStyle: activeStyle
      });
  } else {
    this.state.padStyle.marginTop === 'silver' ?
      this.setState({
        padStyle: inactiveStyle
      }) :
      this.setState({
        padStyle: {
          backgroundColor: 'silver',
          boxShadow: "0 3px silver",
        }
      });
  }
}

    render() {

        return (
            <div className="drum-pad" id={this.props.describeId} onClick={this.playAndDisplay} style={this.state.padStyle}>
                <p>{this.props.keyName}</p>
                <audio ref={ref => this.audio = ref} src={this.props.source} className="clip" id={this.props.keyName}></audio>
            </div>
        )  
    }
}

// component that plays sound when on state and not playing anything when off state
class NowPlaying extends React.Component {

    render() {
        let onPlay;
        this.props.power ?
        onPlay = music.map(i => {
            return (
                <DrumPad describeId={i.id} keyName={i.innerText} source="#" updateDisplay={this.props.updateDisplay} /> // src="#" means it doesn't play any or it is blank
                    )}) :
        onPlay = music.map(i => {
            return (
                <DrumPad describeId={i.id} keyName={i.innerText} source={i.src} updateDisplay={this.props.updateDisplay} />
                    )})

    return (
        <div>{onPlay}</div>
    )

}
}

// Main Component
class App extends React.Component {
    constructor(props) {
      super(props);
      this.state = {
            power: true,
            display: "",
            volumeSlider: 0.3
        }
    }

powerControl() {
        this.setState({
            power: !this.state.power
        });
    }

// function of display when power is on or off
selectPower() {
        if (!this.state.power && this.state.display === "") {
            this.setState({
                display: "Drum Machine!"
                }) 
            }else
            {this.setState({
                display: "Goodbye!"
            });}
            setTimeout(() => this.clearDisplay(), 1000)
        }

//function to call in an onClick event
powerAndDisplayControl() {
    this.powerControl();
    this.selectPower();
}

//handle display or description of sounds
handleDisplay(display) {
    if(this.state.power){
    this.setState({display})
    setTimeout(() => this.clearDisplay(), 1000)
}
}

volumeControl(e) {
    if(this.state.power){
    this.setState({
        volumeSlider: e.target.value,
        display: "Volume:" + Math.round(e.target.value * 100)
    })
    setTimeout(() => this.clearDisplay(), 1000)}
}

clearDisplay() {
    this.setState({display: ""})
}
   
render() {
   const powerSlider = this.state.power ? {float: "left", backgroundColor: "green"} : {float: "right", backgroundColor: "red"};
   const displayStyle = this.state.power ? {backgroundColor: "white"} : {backgroundColor: "dimgray"};
   

   return (
    <div id="container">
    <div id="drum-machine"> 
        <div id="control">
            <div id="title-power">
            <h1 id="title">Drum Machine</h1>
            <div id="power">
                <h4 id="power-text">Power</h4>
                <p id="switch-on">ON</p>
                <p id="switch-off">OFF</p>
                <div id="switch" onClick={this.powerAndDisplayControl.bind(this)}>
                        <div id="inner-slider" style={powerSlider}></div>
                </div>
            </div>
            </div>
            <div id="display" onClick={this.handleDisplay} style={displayStyle}>
                <p>{this.state.display}</p>
            </div>
            <div id="volume-slider">
                <h6 id="range-name-0">0</h6>
                <input className="range-slider" type="range" min="0" max="1" step="0.01" value={this.state.volumeSlider} onChange={this.volumeControl.bind(this)}></input>
                <h6 id="range-name-100">100</h6>
                <h5 id="volume-name">Volume</h5>
            </div>    
        </div>
        <div id="drum-keys">
            <NowPlaying power={!this.state.power} updateDisplay={this.handleDisplay.bind(this)} />
        </div>   
    </div>
    </div>
)
}        
}
ReactDOM.render(<App />, document.getElementById('root'));

